import React, { useEffect, useState } from 'react';
import axios from 'axios';
import api from "../api"

const Payment = () => {
  const [amount, setAmount] = useState(0);
  const [email, setEmail] = useState('');
  const { IMP } = window;
  const token = localStorage.getItem("bbs_access_token");

  const memberNo=localStorage.getItem("id");
  useEffect(() => {
    
    
    console.log(token);
    const nickname = localStorage.getItem("nickname");
    setEmail(localStorage.getItem("email"));
    //  
  }, []);

  const requestPay = async () => {
    
    IMP.init('imp66118428');

    IMP.request_pay({
      pg: 'kakaopay.TC0ONETIME',
      pay_method: 'card',
      merchant_uid: new Date().getTime(),
      name: '  ',
      amount: amount , // 1000   
      buyer_email: email,
    }, async (rsp) => {
      console.log(rsp);
      try {
        const { data } = api.post(`${process.env.REACT_APP_SERVER_URL}/plant-pay-service/verifyIamport/` + rsp.imp_uid, {
          headers: { Authorization: `Bearer ${localStorage.getItem("bbs_access_token")}` }
      });
        if (rsp.paid_amount === data.response.amount) {
          alert(' ');
          //       
          api.post(`${process.env.REACT_APP_SERVER_URL}/plant-pay-service/payMoney`, {
            headers: { Authorization: `Bearer ${localStorage.getItem("bbs_access_token")}` }
        },{
            paymentRequestDto: {
              payMoney: amount,
              memberNo: memberNo
            }
          });
        } else {
          alert(' ');
        }
      } catch (error) {
        console.error('Error while verifying payment:', error);
        alert(' ');
      }
    });
  };

  return (
    <div>
      <input
        type="number"
        value={amount}
        onChange={(e) => setAmount(e.target.value)}
        placeholder=" "
      />
      <button onClick={requestPay}></button>
    </div>
  );
};

export default Payment;
