import axios from "axios";
import { useState, useContext } from "react";
import { useNavigate } from "react-router";
import { AuthContext } from "../../context/AuthProvider";
import { HttpHeadersContext } from "../../context/HttpHeadersProvider";
import myImage from './kakao_login_small.png';
import { Link } from "react-router-dom";
import '../../css/style.css';
import api from "../api";

const CLIENT_ID = process.env.REACT_APP_REST_API_KEY;
const REDIRECT_URI = process.env.REACT_APP_REDIRECT_URL;
const SERVER_URL = process.env.REACT_APP_SERVER_URL;

export const KAKAO_AUTH_URL = `https://kauth.kakao.com/oauth/authorize?client_id=${CLIENT_ID}&redirect_uri=${REDIRECT_URI}&response_type=code`;

function Login() {
  const [notifications, setNotifications] = useState([]);

  const { auth, setAuth } = useContext(AuthContext);
  const { headers, setHeaders } = useContext(HttpHeadersContext);

  const navigate = useNavigate();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const changeId = (event) => {
    setUsername(event.target.value);
  }

  const changePwd = (event) => {
    setPassword(event.target.value);
  }

  const login = async () => {
    const req = {
      username: username,
      password: password
    }

    await api.post(`${SERVER_URL}/plant-service/api/login`, req)
      .then((resp) => {
        console.log("[Login.js] login() success :D");
        console.log(resp.data);

        alert(resp.data.username + ",    ");

        // JWT  
        localStorage.setItem("id", parseInt(resp.data.id));
        localStorage.setItem("bbs_access_token", resp.data.access_token);
        localStorage.setItem("nickname", resp.data.nickname);
        localStorage.setItem("username", resp.data.username);
        localStorage.setItem("email", resp.data.email);

        setAuth(resp.data.id); //   ( )
        setHeaders({"Authorization": `Bearer ${resp.data.jwt}`}); //  Authorization  

        navigate("/");
        window.location.reload();
      }).catch((err) => {
        console.log("[Login.js] login() error :<");
        console.log(err);
        alert(`ID/PW  .`);
      });
  }

  return (
    <div className="login-container">
      <table className="table">
        <tbody>
          <tr>
            <th className="col-3"></th>
            <td>
              <input type="text" value={username} onChange={changeId} size="50px" />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input type="password" value={password} onChange={changePwd} size="50px" />
            </td>
          </tr>
        </tbody>
      </table>
      <br />

      <div className="my-1 d-flex justify-content-center">
        <button className="btn btn-outline-secondary" onClick={login}><i className="fas fa-sign-in-alt"></i> </button>
      </div>
      <div className="text-center">
        <Link to="/findId"> </Link>
        &nbsp;|&nbsp;
        <Link to="/resetMember"> </Link>
      </div>
      <div className="container text-center">
        <p style={{ fontWeight: 'bold', fontSize: '2em' }}>  !</p>
      </div>
      <div className="my-1 d-flex justify-content-center">
        <a href={KAKAO_AUTH_URL} className="kakaobtn">
          <img src={myImage} alt="Kakao login" />
        </a>
      </div>
    </div>
  );
}

export default Login;
