import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import "../../css/WishList.css"; //    CSS 
import api from "../api"

function WishList() {
  const [wishlist, setWishlist] = useState([]);
  const memberId = localStorage.getItem("id");
  const token = localStorage.getItem("bbs_access_token");
  useEffect(() => {
    if (memberId) {
      fetchWishlist(memberId);
    } else {
      alert("  .");
    }
  }, [memberId]);

  const fetchWishlist = async (id) => {
    try {
      const response = await api.get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/goods/${id}`, {headers: {
        Authorization: `Bearer ${token}`,
      },
    });
      setWishlist(response.data);
      console.log(response);
    } catch (error) {
      console.error("Error fetching wishlist:", error);
    }
  };
  const formatDate = (dateString) => {
    console.log(dateString);

    const year = dateString[0]
    const month = dateString[1]
    const day = dateString[2]
    const hours = dateString[3]
    const minutes = dateString[4]
    return `${year} ${month} ${day} ${hours} ${minutes}`;
};
  return (
    <div className="wishlist-container">
      <h2>  </h2>
      <div className="wishlist-list">
        {wishlist.map((item) => (
          <div key={item.id} className="wishlist-item">
            <Link to={`/bbsdetail/${item.id}`} className="bbs-title">
              <div className="item-title">{item.title}</div>
              <div className="item-status">{item.status}</div>
              <div className="item-status">{item.createBy}</div>
              <div className="item-date">{formatDate(item.createdAt)}</div>
            </Link>
          </div>
        ))}
        {wishlist.length === 0 && <p>  .</p>}
      </div>
    </div>
  );
}

export default WishList;
